#CVForm * {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; 
}

#CVForm {
    margin: 1%;
    padding: 1%;   
    background-color: #f3f4f6;
    border-radius: 12px;
}

h2 {
    color: #1f2937;
}

section, .instance {
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}

input {
    height: 2rem;

    padding: 1vmin 1.5vmin;
    border: 1px solid gray;
    border-radius: 12px;
    font-size: 1rem;
}

button {
    height: 3rem;
    font-size: 1rem;
    border: none;
    background-color: #374151;
    border-radius: 6px;
    color: white;
}

.add {
    background-color: #1f2937;
}
#CVPreview {
    margin: 1%;
    padding: 2%; 
    height: 100vh;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    align-self: flex-start;
    border-radius: 12px;
    background-color: whitesmoke;
}

#CVPreview * {
    font-family: Arial, Helvetica, sans-serif;
}

.personal > h1 {
    font-size: 2.5rem;
    padding: 0;
    margin: 0;
    color: #1e3a8a;
}

.personal > h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 2rem;
}

.personal > div {
    margin-bottom: 0.5rem;
    font-size: 1rem;
}

#CVPreview section > h2 {
    color: #1e3a8a;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.instance-preview {
    color: gray;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}


.instance-preview div {
    margin-left: 2rem;
    margin-bottom: 2rem;
    color: black
}

.instance-preview div p {
    margin-top: 1rem;
}

.instance-preview * {
    margin: 0;
    padding: 0;
}
body, html {
    padding: 0;
    margin: 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#root { 
    display: flex;
    flex-direction: column;
}

header {
    background-color: #1f2937;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding: 2.5rem;
}

footer {
    display: flex;
    justify-content: center;
    color: gray;
    align-items: center;
    background-color: lightgray;
    margin-top: auto;
    min-height: 64px;
}

header h1 {
    padding: 0;
    margin: 0;
}

main {
    display: flex;
    flex-flow: row wrap;
}

#CVForm {
    flex: 1 1 50vh;
}

#CVPreview {
    flex: 1 1 50vh;
}

